.baner {
  width: 100%;
  height: calc(100vh - 64px);
  max-height: 960px;
  background-image: url(./assets/images/cuba-tourism-photo-of-a-street.jpg);
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.gradient {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.6)
  );
}

.baner-title {
  color: #fff;
  font-family: Georgia, 'Times New Roman', Times, serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.5);
}

.baner-sub-title {
  color: #fff;
  font-family: Georgia, 'Times New Roman', Times, serif;
  position: absolute;
  bottom: 33px;
  left: 0;
  text-align: center;
  width: 100%;
  font-weight: 400;
  transform: translateY(50%);
}

.baner-sub-title:after,
.baner-sub-title:before {
  width: 30%;
  content: '';
  position: absolute;
  bottom: 10px;
  height: 1px;
  background-color: #fff;
}

.baner-sub-title:after {
  left: 30px;
}

.baner-sub-title:before {
  right: 30px;
}

.border {
  border: 1px solid #fff;
  height: calc(100% - 60px);
  width: calc(100% - 60px);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-bottom: none;
}

.page-nav {
  background-color: #fff;
  border-bottom: 1px solid #d0d4d8;
  height: 64px;
}

.menu {
  display: flex;
  justify-content: center;
  height: 100%;
  transform-origin: top;
}

.btn-toggle {
  display: none;
  position: relative;
}

.btn-toggle:before,
.btn-toggle:after {
  content: '';
  width: 80%;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  height: 2px;
  background-color: #d0d4d8;
}

.btn-toggle:before {
  top: 33.333%;
}

.btn-toggle:after {
  bottom: 33.333%;
}

.link {
  display: block;
  color: #909090;
  text-align: center;
  line-height: 63px;
  padding: 0 8px;
  position: relative;
  font-size: 0.875rem;
}

.link-active {
  font-weight: 600;
  color: #4e5665;
}

.link-active:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #ecb84c;
  bottom: -1px;
  left: 0;
}

.page-section {
  padding: 80px 100px;
  position: relative;
  max-width: 1170px;
  margin: 0 auto;
  text-align: center;
}

.page-section:after {
  content: '';
  height: 1px;
  width: 100%;
  opacity: 0.5;
  background-color: #d0d4d8;
  position: absolute;
  bottom: 0;
  left: 0;
}

.page-section > * {
  margin-bottom: 40px;
}

.page-section > *:last-child {
  margin-bottom: 0;
}

.section-title {
  color: #4e5665;
}
.section-description {
  text-align: left;
  color: #505050;
}
